import './App.css';
import { useState } from 'react';
import arrowDownIcon from './assets/arrow-down-icon.svg';
import arrowUpIcon from './assets/arrow-up-icon.svg';
import Title from './components/Title';
import Subtitle from './components/Subtitle';
import Socials from './components/Socials';

const Border = ({ className }) => <div className={`absolute w-full h-4 bg-black ${className}`} />;

const KacperImg = () => (
  <div
    id="kacper-img"
  />
);

const ScrollBtn = ({ handleScroll, icon, alt }) => (
  <button
    className="mb-8 bg-gray-200 opacity-80 p-4 hover:opacity-100 hover:bg-gray-100"
    type="button"
    onClick={handleScroll}
  >
    <img src={icon} alt={alt} className="arrow-icon opacity-80 hover:opacity-100" />
  </button>
);

const DetailsBox = () => (
  <div
    className="inline-block bg-gray-200 opacity-80 text-4xl text-center p-6 xl:mr-32 text-gray-800"
    style={{
      maxWidth: '550px',
      width: '100%',
    }}
  >
    <p>Hi, I work fulltime for Elsevier based in London.</p>
    <br />
    <p>
      To reach out email me at&nbsp;
      <a
        className="hover:text-black underline"
        href="mailto:kacperturon@gmail.com?subject=Great portfolio site!"
        target="_blank"
        rel="noreferrer"
      >
        kacperturon@gmail.com
      </a>
    </p>
  </div>
);

function App() {
  const [page, setPage] = useState(1);

  const handleScroll = (e) => {
    window.scrollTo({
      top: page === 1 ? document.body.scrollHeight : 0,
      behavior: 'smooth',
    });
    setPage(page === 2 ? 1 : 2);
  };

  return (
    <div
      className="relative w-screen max-w-screen-2xl mx-auto overflow-hidden"
      style={{
        height: '200vh',
      }}
    >
      <div className="absolute z-0 w-full h-full" id="background">
        <Border className="top-0" />
        <KacperImg />
        <Border className="bottom-0" />
      </div>
      <div className="relative z-10" id="foreground">
        <section className="flex items-center justify-between h-screen" id="page-1">
          <Socials />
          <div className="sm:mr-8 flex flex-col justify-between h-full items-end">
            <div />
            <div className="text-black text-right">
              <Title className="mb-8">Kacper Turon</Title>
              <Subtitle className="text-gray-800">FULLSTACK ENGINEER â€¢ LONDON</Subtitle>
            </div>
            <ScrollBtn handleScroll={handleScroll} alt="scroll down" icon={arrowDownIcon} />
          </div>
        </section>

        <section
          className="flex items-center justify-end h-screen"
          id="page-2"
        >
          <div className="sm:mr-8 flex flex-col justify-between h-full items-end">
            <div />
            <DetailsBox />
            <ScrollBtn handleScroll={handleScroll} alt="scroll up" icon={arrowUpIcon} />
          </div>
        </section>
      </div>
    </div>
  );
}

export default App;
